import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { guardarCompromiso, paginationCompromiso, resetCompromisos } from 'src/app/store/actions/compromiso.actions';
import { AppState } from 'src/app/store/app.reducer';
import Swal from 'sweetalert2';
import { SearchPagination } from '../../interfaces/search.pagination';
import { CompromisoModel } from '../../models/gestion-academica/compromiso.model';
import { UsuarioSignModel } from '../../models/externo/usuario-sign.model';
import { CompromisoService } from '../../services/compromiso.service';
import { UtilAlert } from '../../util/util.alert';
import { UtilValidation } from '../../util/util.validation';

@Component({
  selector: 'app-modal-encargado',
  templateUrl: './modal-encargado.component.html',
  styles: [
  ]
})
export class ModalEncargadoComponent implements OnInit {

  @BlockUI() blockUI: NgBlockUI;

  @ViewChild('modalEncargado', { static: false })
  modalEncargado: ElementRef;

  private _subscriptionCompromiso: Subscription;
  public compromiso: CompromisoModel;
  public formEncargado: FormGroup;
  public deshabilitarEliminar: Boolean = true;

  public buscador: String = ""
  public page: number = 1;
  public pageSize: number = 10;
  public searchPagination: SearchPagination<string>;
  public estado: string = "";
  public anio: string = "";

  public usuarioSign: UsuarioSignModel;
  public mensajeError: string;
  public estaRegistrado: Boolean = false;
  public errorSign: Boolean = false;
  public usuarioRolSign: any = null;

  constructor(
    private modalParaEncargado: NgbModal,
    private store: Store<AppState>,
    private utilValidation: UtilValidation,
    public alertService: UtilAlert,
    private toastr: ToastrService,
    private compromisoService: CompromisoService
  ) { }

  ngOnInit(): void {
    this.newFormEncargado();
    this._compromisoState();
  }

  newFormEncargado() {
    this.formEncargado = new FormGroup({
      usuario: new FormControl("", [Validators.required]),
      nombre: new FormControl("", [Validators.required]),
      correo: new FormControl("", [Validators.required, this.utilValidation.emailValido]),
      tipo: new FormControl("", [Validators.required])
    });
    this.formEncargado.get("tipo").setValue("0");
    this.formEncargado.get("nombre").disable();
    this.formEncargado.get("correo").disable();
  }

  openModal(compromiso: CompromisoModel, estado: string, anio: string) {
    this.modalParaEncargado.open(this.modalEncargado, { size: 'sm', ariaLabelledBy: 'modal-basic-title', centered: true }).result.then(() => this.cerrarModal(false), () => this.cerrarModal(false));
    this.compromiso = compromiso;
    this.formEncargado.get("usuario").setValue(this.compromiso.usuarioEncargado);
    this.estado = estado;
    this.anio = anio
    if (compromiso.usuarioEncargado != null) {
      this.formEncargado.get("usuario").setValue(compromiso.usuarioEncargado);
      this.deshabilitarEliminar = false;
      this.buscarSign();
    }
  }

  cerrarModal(dimissAll = true) {
    if (dimissAll) this.modalParaEncargado.dismissAll();
    this.reiniciarForm();
  }

  _compromisoState() {
    this._subscriptionCompromiso = this.store.select("compromiso").subscribe((state) => {
      if (state.searchPagination) {
        this.obtenerPagination(state.searchPagination.seek.toString(), state.searchPagination.page, state.searchPagination.records);
      }
      if (state.compromiso && state.loadedGuardado) {
        this.alertService.successSwal(`Se ha editado correctamente el encargado`);
        this.store.dispatch(resetCompromisos());
        this.cerrarModal();
        this.blockUI.stop();
      }
    });
  }

  guardarEncargado() {
    debugger;
    this.formEncargado.get("nombre").enable();
    this.formEncargado.get("correo").enable();
    UtilValidation.setFormForValidate(this.formEncargado);
    if (this.formEncargado.invalid) {
      this.toastr.info("Debe ingresar correctamente todos los datos antes de guardar");
      this.formEncargado.get("nombre").disable();
      this.formEncargado.get("correo").disable();
      return;
    }
    if (!this.estaRegistrado) {
      if (this.usuarioSign != null) {
        this.compromisoService.saveEncargadoSign(this.usuarioSign).subscribe((value) => {
          if (value.error) {
            this.toastr.error("Se guardará el ejecutivo en el sistema, pero no se ha podido guardar en el sign");
            this.toastr.error(value.mensaje);
          } else {
            this.usuarioSign = value;
          }
        });
      }
    } else {
      console.log("darRolEncargado")
      this.darRolEncargado()
    }
    this.blockUI.start("Guardando...")
    this.setearCompromiso();
    this.compromisoService.saveEncargado(this.compromiso).subscribe((value) => {
      let tipo = this.formEncargado.get("tipo").value
      tipo == "1" ? this.alertService.successSwal(`Se ha editado correctamente el encargado evaluador`) : this.alertService.successSwal(`Se ha editado correctamente el encargado editor`)
      this.listarCompromisos();
      this.cerrarModal();
      this.blockUI.stop();
    }, (error) => {
      this.toastr.error("No se ha logrado guardar el encargado")
      this.blockUI.stop();
    })

    this.formEncargado.get("nombre").disable();
    this.formEncargado.get("correo").disable();
  }

  obtenerPagination(buscador: string, page: number, pageSize: number) {
    this.buscador = buscador;
    this.page = page;
    this.pageSize = pageSize;
    this.searchPagination = {
      seek: buscador,
      page: page,
      records: pageSize
    }
    console.table(this.searchPagination)

  }

  listarCompromisos() {
    this.store.dispatch(paginationCompromiso({ searchPagination: this.searchPagination, anio: this.anio, estado: this.estado }))
    return;
  }

  buscarAD() {
    this.mensajeError = null;
    const encargadoEmail: string = this.formEncargado.get("usuario").value;
    this.compromisoService.getEncargadoByUsuarioAD(encargadoEmail).subscribe((value) => {
      console.log(value)
      if (value.error) {
        if (value.mensaje == "No se encontró el usuario") {
          this.toastr.warning(encargadoEmail + " no se encuentra registrado en Active Directory", "Problemas al Encontrar a " + encargadoEmail)
          this.errorSign = true;
        }
      }
      else if (value.resultado != null) {
        this.usuarioSign = value.resultado;
        this.formEncargado.get("nombre").setValue(this.usuarioSign.nombre);
        this.formEncargado.get("correo").setValue(this.usuarioSign.usuarioEmail + "@uft.cl");
        this.estaRegistrado = false;
        this.errorSign = false;
      }
    })
  }

  buscarSign() {
    if (this.formEncargado.get("usuario").value == "") {
      return;
    }
    this.formEncargado.get("nombre").setValue("");
    this.formEncargado.get("correo").setValue("");
    const encargadoEmail: string = this.formEncargado.get("usuario").value;
    this.mensajeError = null;
    this.compromisoService.getEncargadoByUsuarioEmail(encargadoEmail).subscribe((value) => {
      if (value.error) {
        this.buscarAD()
      } else {
        console.log(value)
        this.usuarioSign = value.resultado
        this.formEncargado.get("nombre").setValue(this.usuarioSign.nombre);
        this.formEncargado.get("correo").setValue(this.usuarioSign.usuarioEmail + "@uft.cl");
        this.estaRegistrado = true;
        this.errorSign = false;
      }
    })
  }

  darRolEncargado() {
    let tipo = this.formEncargado.get("tipo").value;
    this.compromisoService.saveRoleSign(this.usuarioSign, tipo).subscribe((value) => {
      if (value.error) {
        this.toastr.error("No se ha podido dar acceso al encargado a la plataforma", "Error:")
        console.log(value)
      } else {
        console.log(value)
      }
    });
  }

  setearCompromiso() {
    let compromiso = new CompromisoModel();
    compromiso.id = this.compromiso.id;
    compromiso.anio = this.compromiso.anio;
    compromiso.compromisoExtraId = this.compromiso.compromisoExtraId;
    compromiso.estado = this.compromiso.estado;
    compromiso.funcion = this.compromiso.funcion;
    compromiso.porcentajeDocencia = this.compromiso.porcentajeDocencia;
    compromiso.porcentajeGestion = this.compromiso.porcentajeGestion;
    compromiso.porcentajeInvestigacion = this.compromiso.porcentajeInvestigacion;
    compromiso.porcentajeVcm = this.compromiso.porcentajeVcm;
    compromiso.rutDocente = this.compromiso.rutDocente;
    if (this.formEncargado.get("tipo").value == "0") {
      compromiso.usuarioEncargado = this.formEncargado.get("usuario").value;
      compromiso.usuarioEvaluador = this.compromiso.usuarioEvaluador;
    } else if (this.formEncargado.get("tipo").value == "1") {
      compromiso.usuarioEvaluador = this.formEncargado.get("usuario").value;
      compromiso.usuarioEncargado = this.compromiso.usuarioEncargado;
    }
    this.compromiso = compromiso
  }

  reiniciarForm() {
    this.formEncargado.get("tipo").setValue("0");
    this.formEncargado.get("usuario").setValue("");
    this.formEncargado.get("nombre").setValue("");
    this.formEncargado.get("correo").setValue("");
    this.formEncargado.get("usuario").markAsUntouched();
    this.formEncargado.get("nombre").markAsUntouched();
    this.formEncargado.get("correo").markAsUntouched();
    this.deshabilitarEliminar = true;
  }

  changeUsuario() {
    this.formEncargado.get("nombre").setValue("");
    this.formEncargado.get("correo").setValue("");
  }

  cambiarTipo() {
    this.deshabilitarEliminar = true;
    let tipo: string = this.formEncargado.get("tipo").value;
    this.reiniciarForm();
    this.formEncargado.get("tipo").setValue(tipo);

    if (tipo == "0" && this.compromiso.usuarioEncargado != null) {
      this.formEncargado.get("usuario").setValue(this.compromiso.usuarioEncargado)
      this.deshabilitarEliminar = false;
    } else if (tipo == "1" && this.compromiso.usuarioEvaluador != null) {
      this.formEncargado.get("usuario").setValue(this.compromiso.usuarioEvaluador)
      this.deshabilitarEliminar = false;
    }
    this.buscarSign()
  }

  eliminarEncargado() {
    let compromisoNew = { ...this.compromiso };
    let tipo: number = Number(this.formEncargado.get("tipo").value)
    if (tipo === 0) {
      compromisoNew.usuarioEncargado = null;
    } else if (tipo === 1) {
      compromisoNew.usuarioEvaluador = null;
    }
    this.blockUI.start();
    this.compromisoService.saveEncargado(compromisoNew).subscribe((value) => {
      tipo == 1 ? this.alertService.successSwal(`Se ha editado correctamente el encargado evaluador`) : this.alertService.successSwal(`Se ha editado correctamente el encargado editor`)
      this.listarCompromisos();
      this.cerrarModal();
      this.blockUI.stop();
    }, (error) => {
      this.toastr.error("No se ha logrado guardar el encargado")
      this.blockUI.stop();
    })
  }

  showEliminarEncargado() {
    let tipo: number = Number(this.formEncargado.get("tipo").value);
    let title: string = "";
    if (tipo == 0) {
      title = "¿Desea eliminar al encargado editor?"
    } else if (tipo == 1) {
      title = "¿Desea eliminar al encargado evaluador?"
    }
    Swal.fire({
      title: title,
      text: "Esta acción no se puede deshacer",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Aceptar',
      reverseButtons: true,
      cancelButtonText: 'Cancelar',
    }).then((result) => {
      if (result.isConfirmed) {
        this.eliminarEncargado();
      }
    })
  }
}
