import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/app.service';
import { ModalEncargadoComponent } from 'src/app/commons/components/modal-encargado/modal-encargado.component';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { CompromisoModel } from 'src/app/commons/models/gestion-academica/compromiso.model';
import { ConfiguracionCompromisoModel } from 'src/app/commons/models/gestion-academica/configuracion-compromiso.model';
import { EstadoModel } from 'src/app/commons/models/gestion-academica/estado.model';
import { AuthenticationService } from 'src/app/commons/services/authentication.service';
import { ConfiguracionCompromisoService } from 'src/app/commons/services/configuracion-compromiso.service';
import { EstadoService } from 'src/app/commons/services/estado.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { paginationCompromiso, setCompromisoEdit } from 'src/app/store/actions/compromiso.actions';
import { AppState } from 'src/app/store/app.reducer';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-compromiso-list',
  templateUrl: './compromiso-list.component.html',
  styles: [
  ]
})
export class CompromisoListComponent implements OnInit {
  public formTopBar: FormGroup;
  public listaEstados: EstadoModel[];
  public listaConfiguracionCompromiso: ConfiguracionCompromisoModel[] = []
  public page: number = 1;
  public pageSize: number = 10;
  public estado: string = '0';
  public listaCompromisos: CompromisoModel[];
  public buscador: string = "";
  public timer = null;
  public fecha: Date = new Date();
  //public anio: number = this.fecha.getFullYear();
  public anio: number = 2022;
  public anioDefault: number;
  public listaAnios: number[];
  public loaded: boolean;
  public totalElements: number;
  public isEditCompromiso: boolean = false;
  private _subscriptionCompromiso: Subscription;

  @ViewChild(ModalEncargadoComponent, { static: false })
  public modalEncargado: ModalEncargadoComponent;


  constructor(
    private router: Router,
    private appService: AppService,
    public toastr: ToastrService,
    public store: Store<AppState>,
    public alertService: UtilAlert,
    public estadoService: EstadoService,
    public auth: AuthenticationService,
    private configuracionCompromisoService: ConfiguracionCompromisoService,

  ) {
    this.appService.pageTitle = 'Listado de Compromisos';
  }

  ngOnInit(): void {
    this._compromisoState();
    this.traerConfig();
    this.crearTopBar();
    this.getEstados();
    this.listarCompromisos();
  }
  ngOnDestroy(): void {
    this._subscriptionCompromiso.unsubscribe();
  }

  private _compromisoState() {
    this._subscriptionCompromiso = this.store.select("compromiso").subscribe(
      (state) => {
        if (state.paginationCompromiso != null) {
          this.page = state.searchPagination.page;
          this.pageSize = state.searchPagination.records;
        }
        if (state.loaded && state.paginationCompromiso) {
          this.loaded = state.loaded
          this.listaCompromisos = [];
          this.listaCompromisos = state.paginationCompromiso.content;
          this.totalElements = state.paginationCompromiso.totalElements;
        }
      }
    );
  }

  traerConfig() {
    this.configuracionCompromisoService.getAllConfiguracionCompromiso().subscribe((value) => {
      this.listaConfiguracionCompromiso = value
    }, (error) => {
      this.alertService.errorSwal("Ha ocurrido un error inesperado al momento de traer las configuraciones");
    })
  }

  crearTopBar() {
    this.formTopBar = new FormGroup({
      buscador: new FormControl(""),
      registros: new FormControl(""),
      estado: new FormControl(""),
      anio: new FormControl(""),
    });
    this.anioDefault = 2023
    this.formTopBar.get("estado").setValue("0");
    this.estado = this.formTopBar.get("estado").value;
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
    this.formTopBar.get("anio").setValue(this.anioDefault.toString());
  }

  getEstados() {
    this.estadoService.getAllEstado().subscribe((value) => {
      this.listaEstados = value
    }, (error) => {
      this.toastr.error("Ha ocurrido un error al momento de obtener el listado de estados");
    })
  }

  listarCompromisos() {
    this.listaCompromisos = []
    this.loaded = false;
    var anio: string = this.formTopBar.get("anio").value;
    var estado: string = this.formTopBar.get("estado").value;
    this.estado = estado;
    const searchPagination: SearchPagination<string> = {
      page: this.page,
      records: this.pageSize,
      seek: this.buscador ? this.buscador : "",
      order: "rutDocente",
      direction: "asc"
    };
    this.store.dispatch(paginationCompromiso({ searchPagination, estado, anio }))
    console.log(searchPagination);
  }


  buscar(texto: string) {
    this.buscador = texto;
    /**
 .    * Se usa para evitar una petición cada vez que se presione una tecla y se haga solo cuando se termine de escribir.
     **/
    if (this.timer != null) clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      this.listarCompromisos();
      this.timer == null;
    }, 1000);
  }

  abrirCompromiso(compromiso: CompromisoModel) {
    this.store.dispatch(setCompromisoEdit({ compromiso: compromiso }));
    this.router.navigate(["/compromisos/compromiso-form"])
  }

  modoVista(compromiso: CompromisoModel) {
    this.store.dispatch(setCompromisoEdit({ compromiso: compromiso }));
    this.router.navigate(['/compromisos/compromiso-form/view'])
  }

  modoEvaluacion(compromiso: CompromisoModel) {
    this.store.dispatch(setCompromisoEdit({ compromiso: compromiso }));
    this.router.navigate(['/compromisos/compromiso-form/evaluate'])
  }

  verificarConfiguracion(compromiso: CompromisoModel) {
    console.table(this.listaConfiguracionCompromiso)
    this.isEditCompromiso = this.listaConfiguracionCompromiso.find(element => element.facultad.codigo == compromiso.rutDocente.facultad.codigo).isEdit;

  }

  abrirModal(compromiso: CompromisoModel) {
    console.log("abrirModal");
    console.log(compromiso)
    var anio: string = this.formTopBar.get("anio").value;
    console.log(anio)
    var estado: string = this.formTopBar.get("estado").value;
    console.log(estado)
    this.modalEncargado.openModal(compromiso, estado, anio)
  }
}
